import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class ClaimsService {

  constructor(private http: HttpClient) { }

  claimSurveySet(params: any): Observable<any> {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/PaymentRequest/SurveyGroupSet`, params);
  }
  claimSurvey(params: any): Observable<any> {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/PaymentRequest/Survey`, params);
  }

  checkForsharedCode(params: any) {
    return this.http.post(`${environment.BlueNumberDataReqAPI}/api/v1/ShareCode/CreateShareCode`, params);
  }
  getSharedCodeDetails(params: any) {
    return this.http.get(`${environment.BlueNumberDataReqAPI}/api/v1/ShareCode/GetShareCode`, { params: params });
  }
}
